table {
    margin-bottom: 176px;
    padding: var(--space);
    width: 100%;
}

tbody {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column nowrap;
        flex-flow: column nowrap;
}

tbody.reverse-chronological {
    -ms-flex-direction: column-reverse;
        flex-direction: column-reverse;
}

tbody.reverse-chronological tr.head {
    -ms-flex-order: 1;
        order: 1;
}

tr {
    -webkit-animation: fade-in var(--animate);
            animation: fade-in var(--animate);
    -ms-flex-align: center;
        align-items: center;
    display: grid;
    font-size: 14px;
    grid-template-columns: repeat(4, 1fr) 48px;
    grid-gap: var(--space-xs);
    justify-items: flex-start;
    padding: 4px 0;
}

tr:not(.head) {
    border-top: 1px solid var(--gray-light);
}

th, td {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center
}

.flipped {
    -webkit-transform: scaleY(-1);
        -ms-transform: scaleY(-1);
            transform: scaleY(-1);
}



