.rating {
  color: var(--main-text);
  display: -ms-flexbox;
  display: flex;
  font-weight: 700;
  margin-left: -4px;
}

.rating div {
  color: var(--gray-light);
  cursor: pointer;
  -webkit-transition: color var(--animate-fast);
  -o-transition: color var(--animate-fast);
  transition: color var(--animate-fast);
}

.rating div span {
  vertical-align: middle;
}



