.stopwatch {
    background: -webkit-gradient(linear, left top, left bottom, from(var(--tray-bg)), to(white));
    background: -webkit-linear-gradient(var(--tray-bg), white);
    background: -o-linear-gradient(var(--tray-bg), white);
    background: linear-gradient(var(--tray-bg), white);
    bottom: 0;
    -webkit-box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.4);
            box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.4);
    color: var(--main-text);
    left: 0;
    padding: var(--space);
    position: fixed;
    right: 0;
}

.stopwatch .button,
.stopwatch .display {
    text-align: center;
}

.stopwatch .display {
    -ms-flex-align: center;
        align-items: center;
    display: -ms-flexbox;
    display: flex;
    font-size: 60px;
    -ms-flex-pack: center;
        justify-content: center;
    margin: 0 auto var(--space);
}

.stopwatch .button {
    border: 1px solid var(--gray-light);
    border-radius: var(--space);
    cursor: pointer;
    padding: var(--space);
    -webkit-transition: all var(--animate-fast);
    -o-transition: all var(--animate-fast);
    transition: all var(--animate-fast);
}

/* Colon */

.colon {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column nowrap;
        flex-flow: column nowrap;
    height: 20px;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.colon::before, .colon::after {
    background: var(--main-text);
    border-radius: 4px;
    content: '';
    height: 4px;
    margin: 0 2.5px;
    width: 4px;
}



